<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">推荐用户列表</h3>
            <div class="top-btn">
              <div>
                <label>用户名：</label>
                <input v-model="username">
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="uid" label="用户id"></el-table-column>
                    <el-table-column prop="username" label="用户名"></el-table-column>
                    <el-table-column prop="mobile" label="手机号"></el-table-column>
                    <el-table-column prop="vip_level" label="用户vip等级"></el-table-column>
                    <el-table-column prop="invite_count" label="总邀请人数"></el-table-column>
                    <!-- <el-table-column prop="parent_uid" label="父亲用户id"></el-table-column> -->
                    <el-table-column prop="total_income" label="总收益"></el-table-column>
                    <el-table-column prop="create_time" label="注册时间"></el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <div>
                    <span slot="footer" class="btn-footer">
                        <router-link :to="{name:'UserListManage'}">
                            <el-button type="info" class="topbtn-btn">返回</el-button>
                        </router-link>
                    </span>
                </div>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { queryProfitGraph } from "@/api/public.js";
import moment from "moment";

export default {
  name: "UserAddListManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      username: "",
      child_uid: "",
      time:[],
      getData: [],
      getData2: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: "",
      members: [
        {
          value: "",
          label: "请选择"
        },
        {
          value: 0,
          label: "待支付"
        },
        {
          value: 1,
          label: "已支付"
        }
      ],
      loading:false,
      result_list:[],
      detailDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "4";
  },
  mounted() {
    this.child_uid = this.$route.query.child_uid;
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        username: this.username,
        child_uid: this.child_uid,
        page_index: this.currentRow,
        page_size: 10
      };
      queryProfitGraph(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              data.total_income = (Number(data.total_income)/100).toFixed(2);
              var newDate = new Date();
              newDate.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.username = "";
      this.currentRow = 1;
      this.getAllData();
    },
  }
};
</script>

<style scoped>
</style>



