<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">修改账号密码</h3>
            <div class="top-btn">
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="updateInfo()">修改密码</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-dialog title="修改密码" :visible.sync="editDialogVisible" width="30%">
                    <div class="des-dialog">
                      <div class="dialog_add">
                        <label>请输入旧密码: </label>
                        <input v-model="old_pwd">
                      </div>
                      <div class="dialog_add">
                        <label>请输入新密码: </label>
                        <input v-model="new_pwd">
                      </div>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="editDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureAllSent()">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { updatePwdByOldPwd } from "@/api/public.js";
import moment from "moment";

export default {
  name: "updatePassword",
  data() {
    return {
      loginData: {}, //登陆的数据
      old_pwd: "",
      new_pwd: "",
      editDialogVisible: false,
      tishiDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "6";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
    }
  },
  methods: {
    //修改密码
    updateInfo() {
      this.editDialogVisible = true;
    },
    //确认修改密码
    sureAllSent() {
      this.editDialogVisible = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        old_pwd: this.old_pwd,
        new_pwd: this.new_pwd
      };
      updatePwdByOldPwd(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success('密码修改成功');
          this.$router.push({name:'ComLogin'});
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
  }
};
</script>

<style scoped>
</style>



