<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">统计数据</h3>
            <div class="top-btn">
              <div>
                <label>起止时间：</label>
                <el-date-picker class="seltime" v-model="time" type="daterange" range-separator="至"
                    start-placeholder="开始日期" end-placeholder="结束日期">
                </el-date-picker>
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="gx_order_amount" label="已支付升级订单总金额"></el-table-column>
                    <el-table-column prop="withdraw_amount" label="已提现金额"></el-table-column>
                </el-table>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { staticsGxOrder } from "@/api/public.js";
import moment from "moment";

export default {
  name: "totalBalance",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      time:[],
      getData: []
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "8";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    getAllData() {
      if (this.time=="" || this.time == null) {
        var begin = "";
        var end = "";
      } else {
        begin = new Date(this.time[0]);
        begin = Date.parse(begin)/1000;
        end = new Date(this.time[1]);
        end = Date.parse(end)/1000;
      }
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        start_time: begin,
        end_time: end
      };
      staticsGxOrder(params).then(res => {
        if (res.data.code === 0) {
          this.getData = [];
          this.getData.push(res.data.data);
          this.getData.forEach(data => {
            data.gx_order_amount = Number(data.gx_order_amount).toFixed(2);
            data.withdraw_amount = Number(data.withdraw_amount).toFixed(2);
          });
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.time = "";
      this.getAllData();
    },
  }
};
</script>

<style scoped>

/* 头部数据显示 */

.data_top {
  width: 100%;
  background: #E9EEF3;
  padding-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  text-align: left;
}

.data_con {
  width: 22%;
  margin-right: 4%;
  display: flex;
  flex-direction: column;
  background: white;
  padding: 20px 20px 10px 20px;
}

.data_top div:last-child {
  margin-right: 0px;
}

.bottom_data {
  text-align: left;
  color: #585858;
  font-size: 14px;
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #DEE2E7;
}

.total {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 26px;
  font-weight: bold;
}
</style>



