<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">用户列表</h3>
            <div class="top-btn">
              <div>
                <label>用户名：</label>
                <input v-model="username">
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="username" label="用户名"></el-table-column>
                    <el-table-column prop="mobile" label="手机号"></el-table-column>
                    <el-table-column prop="nickname" label="昵称"></el-table-column>
                    <el-table-column prop="create_time" label="注册时间"></el-table-column>
                    <el-table-column label="操作">
                      <template slot-scope="scope">
                        <a class="special" @click="showDetail(scope.row.id)">查看详情</a>
                        <a class="special3" @click="showList(scope.row.id)" style="margin-left:5px">直推好友</a>
                        <a class="special2" @click="showLogs(scope.row.id)" style="margin-left:5px">余额日志</a>
                      </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="用户详情" :visible.sync="detailDialogVisible" width="35%">
                    <div class="des-dialog">
                      <el-table ref="singleTable" :data="getData2" highlight-current-row border
                style="width:100%;border-left: 1px solid #ebeef5;">
                        <el-table-column prop="balance" label="账户金额"></el-table-column>
                        <!-- <el-table-column prop="username" label="用户名称"></el-table-column> -->
                        <el-table-column prop="idcode" label="推荐码"></el-table-column>
                        <el-table-column prop="vip_level" label="vip级别"></el-table-column>
                        <el-table-column prop="invite_username" label="推荐人">
                          <template slot-scope="scope">
                            <a class="special" @click="showDetail2(scope.row.invite_uid)">{{scope.row.invite_username}}</a>
                          </template>
                        </el-table-column>
                        <el-table-column label="操作">
                          <template slot-scope="scope">
                            <a class="special" @click="editVip(scope.row.id)">修改级别</a>
                            <a class="special3" @click="frozenWith(scope.row.id,0)" v-if="scope.row.frozen_withdraw==0">禁止提现</a>
                            <a class="special2" @click="frozenWith(scope.row.id,1)" v-if="scope.row.frozen_withdraw==1">启动提现</a>
                          </template>
                        </el-table-column>
                      </el-table>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="detailDialogVisible = false">关 闭</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="修改VIP级别" :visible.sync="editDialogVisible" width="30%">
                    <div class="des-dialog">
                      <div class="dialog_add">
                        <label>请输入级别(0-10): </label>
                        <input v-model="vip_level" type="number">
                      </div>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="editDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureEditVip()">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserListInfo , UserDetailInfo , frozenUserWithdraw , unfrozenUserWithdraw , modifyLevel } from "@/api/public.js";
import moment from "moment";

export default {
  name: "UserListManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      user_id: "",
      vip_level: "",
      username: "",
      time:[],
      getData: [],
      getData2: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: "",
      members: [
        {
          value: "",
          label: "请选择"
        },
        {
          value: 0,
          label: "待支付"
        },
        {
          value: 1,
          label: "已支付"
        }
      ],
      loading:false,
      result_list:[],
      detailDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "4";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        username: this.username,
        page_index: this.currentRow,
        page_size: 10
      };
      UserListInfo(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              var newDate = new Date();
              newDate.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.username = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //查看用户详情
    showDetail(id){
      this.getData2 = [];
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        user_id: id
      };
      UserDetailInfo(params).then(res => {
        if (res.data.code === 0) {
          let obj = res.data.data;
          obj.balance = Number(obj.balance/100).toFixed(2);
          this.getData2.push(obj);
        } else {
            this.$message.error(res.data.msg);
        }
        this.detailDialogVisible = true;
      });
    },
    //查看推荐人详情
    showDetail2(id){
      this.getData2 = [];
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        user_id: id
      };
      UserDetailInfo(params).then(res => {
        if (res.data.code === 0) {
          let obj = res.data.data;
          obj.balance = Number(obj.balance/100).toFixed(2);
          this.getData2.push(obj);
        } else {
            this.$message.error(res.data.msg);
        }
        this.detailDialogVisible = true;
      });
    },
    //直推好友列表
    showList(id){
      this.$router.push({name:'UserAddListManage',query:{child_uid:id}});
    },
    //用户余额日志列表
    showLogs(id){
      this.$router.push({name:'UserWalletLogManage',query:{id:id}});
    },
    //修改VIP级别
    editVip(id){
      this.user_id = id;
      this.editDialogVisible = true;
    },
    //确定修改
    sureEditVip(){
      modifyLevel({user_id:this.user_id,vip_level:this.vip_level}).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getData2[0].vip_level = this.vip_level;
        } else {
          this.$message.error(res.data.msg);
        }
        this.editDialogVisible = false;
      });
    },
    //禁止、启动提现功能
    frozenWith(id,num){
      if(num==0){
        frozenUserWithdraw({user_id:id}).then(res => {
          if (res.data.code === 0) {
            this.$message.success(res.data.msg);
            this.getAllData();
          } else {
            this.$message.error(res.data.msg);
          }
          this.detailDialogVisible = false;
        });
      }else{
        unfrozenUserWithdraw({user_id:id}).then(res => {
          if (res.data.code === 0) {
            this.$message.success(res.data.msg);
            this.getAllData();
          } else {
            this.$message.error(res.data.msg);
          }
          this.detailDialogVisible = false;
        });
      }
    }
  }
};
</script>

<style scoped>
</style>



