<template>
    <div>
        <el-header class="title-text">
            <div>国学培训管理系统</div>
            <div class="fr">
                <img src="../../assets/images/person-logo.png">
                <div class="logtext">
                    <label>{{loginData.nickname}}</label>
                    <span @click="logout">退出</span>
                </div>
            </div>
        </el-header>
    </div>

</template>
<script>
import axios from "axios";
import Vue from "vue";
import { updateUserLogoutmobile } from "@/api/public.js";
    export default{
        name:'Header',
        data(){
          return{
            loginData: {}, //登陆的数据
          }
        },
        mounted(){
          // if(!document.cookie){
          //   this.$router.push({name:'ComLogin'});
          // }
          if(!!localStorage.getItem('loginData')){
            this.loginData = JSON.parse(localStorage.getItem('loginData'));
          }else{
            this.$router.push({name:'ComLogin'})
          }
        },
        methods:{

          logout() {
            let params = {
              uid: this.loginData.id,
              s_id: this.loginData.sid
            };
            updateUserLogoutmobile(params).then(res => {
              if (res.data.code === 0) {
                localStorage.setItem('loginData','');
                this.$router.push({ name: "ComLogin" });
              } else {
                this.$message.error(res.data.msg);
              }
            });
          },
        }
    }
</script>
<style scoped>
    .el-header {
        color: #000;
        text-align: center;
        height: 80px !important;
        line-height: 80px;
    }
    .title-text{
        text-align: left;
        color: #FFEB09;
        background: #333;
        margin-bottom: 10px;
        position: relative;
        display: flex;
        justify-content: space-between;
    }
    .head-two{
        background-color: #fff;
        height: 80px !important;
        margin-bottom: 24px;
    }
    .fr{
        margin-top: 20px;
        display: flex;
        justify-content: flex-start;
        height: 40px;
        line-height: 40px;
    }
    .logtext{
        margin-left: 10px;
        color: white;
    }
    .logtext span{
        color: red;
        font-size: 12px;
    }
    a{
     color: #333;
    }
    .head-nav{
        float: left;
        margin-left: 20%;
        cursor: pointer;
    }
    .top-ul{
        display: inline;
        white-space: normal;
        color: #333;
        margin-left: -20px;
    }
    .top-ul li{
        display: inline-block;
        margin-right: 40px;
        cursor: pointer;
    }
    .top-ul span{
        margin-right: 25px;
        color: #7f7f7f;
    }
</style>




