<template>
  <div class="home">
    <div id="backbg" class="home-one">
        <h1 class="home-title">国学培训后台管理系统</h1>
    </div>
    <div class="home-login">
        <div>
            <label>用户名:</label>
            <input v-model="account" class="login-input" type="text">
        </div>
        <div class="password">
            <label class="login-label">密</label>码:
            <input v-model="password" class="login-input" type="password" @keyup.13="goLogin">
        </div>
        <p v-if="wrong" class="login-p">密码输入不正确</p>
        <div>
            <el-button class="login-btn" @click="goLogin">登录</el-button>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue';
import commonURL from "../../assets/js/ajax";
import { log_in } from "@/api/public.js";

export default {
  name: 'ComLogin',
  data(){
    return{
      account:'',
      password:'',
      haveTap: 0, // 屏蔽重复点击
      wrong:false
    }
  },
  mounted:function(){
    var orderHeight = document.body.clientHeight;
    document.getElementById('backbg').style.height = (orderHeight*0.5)+'px';
  },
  methods:{
      goLogin:function(){
        const that = this;
        if (that.haveTap === 0) {
          that.haveTap = 1;
          let params = {
            username: that.account,
            password: that.password
          };
          log_in(params).then(res => {
             that.haveTap = 0;
            if (res.data.code === 0) {
              that.$cookies.set("status", "logined", '7d'); // 设置cookie中存放的生命周期
              that.$message.success("登陆成功");
              that.$router.push({name:'orderManage'})
              localStorage.setItem('loginData',JSON.stringify(res.data.data));
            } else {
              that.$message.error(res.data.msg);
            }
          });
        } else {
          console.log("屏蔽重复点击");
          that.$message.error("点击频率过快");
        }
      }
  }
}
</script>

<style scoped>
    .home{
        width: 100%;
        position: relative;
    }
    .home-one{
        position: relative;
        width: 100%;
        background: #333;
    }
    .home-title{
        position: absolute;
        color: #FFE900;
        margin: 0 auto;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .password{
        margin-top: 10px;
    }
    .login-label{
        letter-spacing: 1rem;
    }
    .home-login{
        position: absolute;
        margin: 0 auto;
        width: 30%;
        left: 50%;
        transform: translate(-50%,-50%);
        border: 1px solid #fff;
        background: #fff;
        padding: 30px;
        border-radius: 5px;
    }
    .login-input{
        border: 1px solid #ccc;
        border-radius: 3px;
        width: 44%;
        padding: 8px;
    }
    .login-p{
        font-size: 14px;
        color:#FF0000;
        margin-bottom: 0px;
    }
    .login-btn{
        background: #262626;
        color: #fff;
        padding: 10px 40px;
        margin-top: 10px;
    }
</style>



