<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">资金账号</h3>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="type_no" label="钱包类型"></el-table-column>
                    <el-table-column prop="balance" label="账户余额"></el-table-column>
                    <el-table-column prop="profit_ratio" label="利润率"></el-table-column>
                    <el-table-column prop="remark" label="说明"></el-table-column>
                    <el-table-column label="操作">
                      <template slot-scope="scope">
                        <a class="special" @click="showDetail(scope.row.id)">查看详情</a>
                      </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { PlatformInfo , PlatformHistory } from "@/api/public.js";
import moment from "moment";

export default {
  name: "platformManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      mobile: "",
      time:[],
      getData: [],
      getData2: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      loading:false,
      result_list:[],
      detailDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "5";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: this.currentRow,
        page_size: 10
      };
      PlatformInfo(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data;
            this.totalPage = Number(res.data.data.length);
            this.getData.forEach(data => {
              data.balance = Number(data.balance).toFixed(2);
              data.profit_ratio = data.profit_ratio + '%';
              if(data.type_no == 'balance'){
                data.type_no = '平台总余额资金';
              }else if(data.type_no == 'fund_creator'){
                data.type_no = '创始人截留资金';
              }else if(data.type_no == 'pay1_fee'){
                data.type_no = '支付通道手续费支出资金';
              }else{
                data.type_no = '其他';
              }
            });
          } else {
            this.$message.error(res.data.msg);
          }
      });
    },
    showDetail(id){
      this.$router.push({name:'platformLogs',query:{id:id}});
    }
  }
};
</script>

<style scoped>
</style>



