<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">用户余额日志</h3>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="uid" label="用户id"></el-table-column>
                    <el-table-column prop="log_type" label="类型"></el-table-column>
                    <el-table-column prop="content" label="说明"></el-table-column>
                    <el-table-column prop="change_money" label="金额"></el-table-column>
                    <el-table-column prop="create_time" label="创建时间"></el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <div>
                    <span slot="footer" class="btn-footer">
                        <router-link :to="{name:'UserListManage'}">
                            <el-button type="info" class="topbtn-btn">返回</el-button>
                        </router-link>
                    </span>
                </div>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { queryUserWalletLog } from "@/api/public.js";
import moment from "moment";

export default {
  name: "UserWalletLogManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      mobile: "",
      time:[],
      getData: [],
      getData2: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: "",
      members: [
        {
          value: "",
          label: "请选择"
        },
        {
          value: 0,
          label: "待支付"
        },
        {
          value: 1,
          label: "已支付"
        }
      ],
      loading:false,
      result_list:[],
      detailDialogVisible: false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "4";
  },
  mounted() {
    this.id = this.$route.query.id;
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        user_id: this.id,
        page_index: this.currentRow,
        page_size: 10
      };
      queryUserWalletLog(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              if(data.log_type == 'deposit'){
                data.log_type = "收入";
              } else if(data.log_type == 'withdraw'){
                data.log_type = "提现";
              } else if(data.log_type == 'freeze'){
                data.log_type = "资金冻结";
              } else if(data.log_type == 'unfreeze'){
                data.log_type = "资金解冻";
              } else if(data.log_type == 'unfreeze_back'){
                data.log_type = "解冻资金回退到账户";
              }
              data.change_money = (Number(data.change_money)/100).toFixed(2);
              var newDate = new Date();
              newDate.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    }
  }
};
</script>

<style scoped>
</style>



