import Vue from 'vue';
import Router from 'vue-router';
import ComLogin from '@/components/page/ComLogin';   //登录页
import WithdrawManage from '@/components/page/WithdrawManage'; //提现管理
import UserIdCardManage from '@/components/page/UserIdCardManage'; //用户实名认证管理
import orderManage from '@/components/page/orderManage'; //vip订单管理
import UserListManage from '@/components/page/UserListManage'; //用户列表
import platformManage from '@/components/page/platformManage'; //资金账户列表
import platformLogs from '@/components/page/platformLogs'; //变动日志
import updatePassword from '@/components/page/updatePassword'; //修改密码
import UserAddListManage from '@/components/page/UserAddListManage'; //邀请好友列表
import totalBalance from '@/components/page/totalBalance'; //总统计金额
import UserWalletLogManage from '@/components/page/UserWalletLogManage'; //用户余额日志列表

Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/ComLogin',
      name: 'ComLogin',
      component: ComLogin
    },{
      path: '/Main/WithdrawManage',
      name: 'WithdrawManage',
      component: WithdrawManage
    },{
      path: '/Main/UserIdCardManage',
      name: 'UserIdCardManage',
      component: UserIdCardManage
    },{
      path: '/Main/orderManage',
      name: 'orderManage',
      component: orderManage
    },{
      path: '/Main/UserListManage',
      name: 'UserListManage',
      component: UserListManage
    },{
      path: '/Main/platformManage',
      name: 'platformManage',
      component: platformManage
    },{
      path: '/Main/platformLogs',
      name: 'platformLogs',
      component: platformLogs
    },{
      path: '/Main/updatePassword',
      name: 'updatePassword',
      component: updatePassword
    },{
      path: '/Main/UserAddListManage',
      name: 'UserAddListManage',
      component: UserAddListManage
    },{
      path: '/Main/totalBalance',
      name: 'totalBalance',
      component: totalBalance
    },{
      path: '/Main/UserWalletLogManage',
      name: 'UserWalletLogManage',
      component: UserWalletLogManage
    },
  ]
});



