<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">变动日志</h3>
            <div class="order-tabs">
              <el-table ref="singleTable" :data="getData" highlight-current-row border
              style="width:100%;">
                  <el-table-column prop="income" label="收入支出"></el-table-column>
                  <el-table-column prop="remark" label="说明"></el-table-column>
                  <el-table-column prop="create_time" label="时间"></el-table-column>
              </el-table>
              <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
              </el-pagination>
              <div>
                  <span slot="footer" class="btn-footer">
                      <router-link :to="{name:'platformManage'}">
                          <el-button type="info" class="topbtn-btn">返回</el-button>
                      </router-link>
                  </span>
              </div>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { PlatformInfo , PlatformHistory } from "@/api/public.js";
import moment from "moment";

export default {
  name: "platformLogs",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      loading:false,
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "5";
  },
  mounted() {
    this.id = this.$route.query.id;
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData(this.id);
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData(this.id);
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData(id) {
      let params = {
        id: id,
        uid: this.loginData.id,
        sid: this.loginData.sid,
        page_index: this.currentRow,
        page_size: 10
      };
      PlatformHistory(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              if(data.type_no == 'balance'){
                data.type_no = '平台总余额资金';
              }else if(data.type_no == 'fund_creator'){
                data.type_no = '创始人截留资金';
              }else if(data.type_no == 'pay1_fee'){
                data.type_no = '支付通道手续费支出资金';
              }else{
                data.type_no = '其他';
              }
              data.income = Number(data.income).toFixed(2);
              var newDate = new Date();
              newDate.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
            this.$message.error(res.data.msg);
          }
      });
    }
  }
};
</script>

<style scoped>
</style>



