import Vue from 'vue'
import axios from "axios";

// const URL = "/dev/api";
const URL = "http://api.guoxuekong.com/index.php/admin";
// const URL = "http://gx.lezchou.com/index.php/admin";
export const getUrl = URL;
const getAllParams = {
  app_type: process.env.NODE_ENV === 'admin_pc',// 【公共】应用类型支持ios、web、android, undefined, text,选填
  app_version: "1.0.0",// 【公共】应用版本类似1.0.0, , text,选填
  notify_id: 1231232,// 【公共】请求流水id唯一, , text,选填
  app_request_time: 100000,// 【公共】请求时间戳 秒级, , text,选填
  lang: "zh-cn",// 【公共】支持zh-cn、en, zh-cn, text,选填
  client_id: "by04esfH0glAa1",// 【公共】应用id，分配得到每个接口必传, , text,选填
  service_version: "100",// 【公共】请求服务的版本，默认100, 100, text,选填
  sign: "",
  uid: 0,
  sid: "",
  buss_data: "",
  country_no: "86"
}

//    登录 (手机号 + 密码)
// POST
const log_in_params = {
  ...getAllParams,
  service_type: "by_UserLoginSession_loginByUsername",// 【公共】请求服务类型, by_UserLoginSession_loginByUsername, text,选填
  username: "",// 用户名（登录用户名 6-24英文数字)
  device_token: "",// 设备标识, , text,选填
  device_type: "",// 设备类型(获取浏览器的类型), , text,选填
  password: "",//密码（8-24位)
}

export const log_in = (data) => {
  return axios({
    method: "post",
    url: URL,
    data: { ...log_in_params,...data }
  })
}

