<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">提现管理</h3>
            <div class="top-btn">
              <div>
                <label>审核状态：</label>
                <el-select v-model="member" placeholder="请选择" class="select_input">
                    <el-option
                    v-for="item in members"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
              </div>
            </div>
            <div class="top-btn">
              <div>
                <label>起止时间：</label>
                <el-date-picker class="seltime" v-model="time" type="daterange" range-separator="至"
                    start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions">
                </el-date-picker>
              </div>
              <div class="search_btns">
                <el-button type="primary" icon="el-icon-download" @click="download">导出</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="audit_nick" label="审核人姓名"></el-table-column>
                    <el-table-column prop="amount" label="金额"></el-table-column>
                    <el-table-column prop="to_wallet_info" label="提现信息"></el-table-column>
                    <el-table-column prop="create_time" label="申请时间"></el-table-column>
                    <el-table-column prop="audit_status" label="审核状态">
                      <template slot-scope="scope">
                          <label v-if="scope.row.audit_status == 0">待审核</label>
                          <label v-if="scope.row.audit_status == 1" class="special2">通过</label>
                          <label v-if="scope.row.audit_status == 2" class="delete_color">驳回</label>
                      </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <a class="special" @click="passApply(scope.row)" v-if="scope.row.audit_status != 1">通过</a>
                            <a class="delete_color" @click="refuseApply(scope.row)" v-if="scope.row.audit_status != 1">拒绝</a>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="审核提示" :visible.sync="statusDialogVisible" width="30%">
                    <div class="des-dialog">
                        <p>此操作将通过审核，是否继续?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureStatus()">确 定</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="审核提示" :visible.sync="statusDialogVisible2" width="30%">
                    <div class="des-dialog">
                        <p>此操作将拒绝审核，是否继续?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible2 = false">取 消</el-button>
                        <el-button type="primary" @click="sureStatus2()">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { queryWithdraw , passWithdraw , denyWithdraw , exportWithdraw } from "@/api/public.js";
import moment from "moment";

export default {
  name: "WithdrawManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      statusDialogVisible: false,
      statusDialogVisible2: false,
      time:[],
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      member: 0,
      members: [
        // {
        //   value: "",
        //   label: "请选择"
        // },
        {
          value: 0,
          label: "待审核"
        },
        {
          value: 1,
          label: "通过"
        },
        {
          value: 2,
          label: "驳回"
        }
      ],

      pickerOptions:{
        //日期选择问题
        onPick: ({ maxDate, minDate }) => {
          this.pickerMinDate = minDate.getTime();
          if (maxDate) {
            this.pickerMinDate = "";
          }
        },
        disabledDate: time => {
          if (this.pickerMinDate !== "") {
            const day30 = (30 - 1) * 24 * 3600 * 1000;
            let maxTime = this.pickerMinDate + day30;
            if (maxTime > new Date()) {
              maxTime = new Date();
            }
            return time.getTime() > maxTime;
          }
          return time.getTime() > Date.now();
        }
      },
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "2";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      var day3 = new Date();
      day3.setTime(day3.getTime()-24*60*60*1000);
      var begin = this.$moment(new Date(day3)).format("YYYY-MM-DD");
      var end = this.$moment(new Date()).format("YYYY-MM-DD");
      this.time.push(begin);
      this.time.push(end);
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        audit_status: this.member,
        page_index: this.currentRow,
        page_size: 10
      };
      queryWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.getData = res.data.data.list;
          this.totalPage = Number(res.data.data.count);
          this.getData.forEach(data => {
            data.amount = Number(data.amount/100).toFixed(2);
            var newDate = new Date();
            newDate.setTime(data.create_time * 1000);
            data.create_time = this.$moment(newDate).format(
            "YYYY-MM-DD HH:mm:ss"
            );
          });
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.member = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //通过提现申请
    // clickGet(row) {
    //   this.loading = true;
    //   setTimeout(() => {
    //     this.loading = false;
    //     this.getSet(row);
    //   }, 1000);
    // },

    //通过提现申请
    passApply(row) {
      this.id = row.id;
      this.statusDialogVisible = true;
    },
    //通过提现申请
    sureStatus() {
      this.statusDialogVisible = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      passWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    // 拒绝提现申请
    refuseApply(row) {
      this.id = row.id;
      this.statusDialogVisible2 = true;
    },
    //拒绝提现申请
    sureStatus2() {
      this.statusDialogVisible2 = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      denyWithdraw(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    download() {
      if (this.time=="" || this.time == null) {
        var day3 = new Date();
        day3.setTime(day3.getTime()-24*60*60*1000);
        var begin = new Date(day3);
        begin = Date.parse(begin)/1000;
        var end = new Date();
        end = Date.parse(end)/1000;
      } else {
        begin = new Date(this.time[0]);
        begin = Date.parse(begin)/1000;
        end = new Date(this.time[1]);
        end = Date.parse(end)/1000;
      }
      let params = {
          uid: this.loginData.id,
          sid: this.loginData.sid,
          start_time: begin,
          end_time: end
      };
      exportWithdraw(params).then(res => {
        const blob = new Blob([res.data])
        const fileName = '提现记录.xlsx'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      });
    },
  }
};
</script>

<style scoped>

/* 头部数据显示 */

.data_top {
  width: 100%;
  background: #E9EEF3;
  padding-bottom: 10px;
  display: flex;
  justify-content: flex-start;
  text-align: left;
}

.data_con {
  width: 22%;
  margin-right: 4%;
  display: flex;
  flex-direction: column;
  background: white;
  padding: 20px 20px 10px 20px;
}

.data_top div:last-child {
  margin-right: 0px;
}

.bottom_data {
  text-align: left;
  color: #585858;
  font-size: 14px;
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #DEE2E7;
}

.total {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 26px;
  font-weight: bold;
}
</style>



