<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg">
            <h3 class="order-top">用户实名认证管理</h3>
            <div class="top-btn">
                <div>
                    <label>审核状态：</label>
                    <el-select v-model="member" placeholder="请选择" class="select_input">
                        <el-option
                        v-for="item in members"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div class="search_btns">
                    <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
                </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="real_name" label="姓名"></el-table-column>
                    <el-table-column prop="card_no" label="身份证号码"></el-table-column>
                    <el-table-column prop="front_img" label="身份证正面">
                      <template slot-scope="scope">
                        <img :src="scope.row.front_img">
                      </template>
                    </el-table-column>
                    <el-table-column prop="back_img" label="身份证反面">
                      <template slot-scope="scope">
                        <img :src="scope.row.back_img">
                      </template>
                    </el-table-column>
                    <el-table-column prop="verify" label="状态">
                        <template slot-scope="scope">
                            <label v-if="scope.row.verify == 0" class="special">未审核</label>
                            <label v-if="scope.row.verify == -1" class="delete_color">审核失败</label>
                            <label v-if="scope.row.verify == 1" class="special2">审核通过</label>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <a class="special2" @click="passApply(scope.row)" v-if="scope.row.verify == 0">通过</a>
                            <a class="delete_color" @click="refuseApply(scope.row)" v-if="scope.row.verify == 0">拒绝</a>
                        </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="审核提示" :visible.sync="statusDialogVisible" width="30%">
                    <div class="des-dialog">
                        <p>此操作将通过审核，是否继续?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureStatus()">确 定</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="审核提示" :visible.sync="statusDialogVisible2" width="30%">
                    <div class="des-dialog">
                        <p>此操作将拒绝审核，是否继续?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="statusDialogVisible2 = false">取 消</el-button>
                        <el-button type="primary" @click="sureStatus2()">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { UserIdCardInfo , passUserIdCard , denyUserIdCard } from "@/api/public.js";
import moment from "moment";

export default {
  name: "UserIdCardManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      statusDialogVisible: false,
      statusDialogVisible2: false,
      member: 0,
      members: [
        // {
        //   value: "",
        //   label: "请选择"
        // },
        {
          value: 0,
          label: "未审核"
        },
        {
          value: 1,
          label: "审核通过"
        },
        {
          value: -1,
          label: "审核失败"
        }
      ],
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {}
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "3";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        verify: this.member,
        page_index: this.currentRow,
        page_size: 10
      };
      UserIdCardInfo(params).then(res => {
        if (res.data.code === 0) {
          this.getData = res.data.data.list;
          this.totalPage = Number(res.data.data.count);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.member = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //通过申请
    passApply(row) {
      this.id = row.id;
      this.statusDialogVisible = true;
    },
    //通过申请
    sureStatus() {
      this.statusDialogVisible = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      passUserIdCard(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    // 拒绝申请
    refuseApply(row) {
      this.id = row.id;
      this.statusDialogVisible2 = true;
    },
    //拒绝申请
    sureStatus2() {
      this.statusDialogVisible2 = false;
      let params = {
        uid: this.loginData.id,
        sid: this.loginData.sid,
        id: this.id
      };
      denyUserIdCard(params).then(res => {
        if (res.data.code === 0) {
          this.$message.success(res.data.msg);
          this.getAllData();
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
  }
};
</script>

<style scoped>
</style>



