<template>
  <div>
    <Index-Header></Index-Header>
    <el-container>
      <Index-Aside></Index-Aside>
      <el-main>
          <div class="main-bg" v-loading="loading" element-loading-text="已发送请求" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
            <h3 class="order-top">VIP订单管理</h3>
            <div class="top-btn">
              <div>
                <label>订单号：</label>
                <input v-model="order_no">
              </div>
              <div>
                <label>用户名：</label>
                <input v-model="username">
              </div>
              <div>
                  <label>支付状态：</label>
                  <el-select v-model="pay_status" placeholder="请选择" class="select_input">
                      <el-option
                      v-for="item in members"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      </el-option>
                  </el-select>
              </div>
              <div class="search_btns">
                <el-button type="primary" class="topbtn-btn" @click="searchInfo()">查询</el-button>
              </div>
            </div>
            <div class="top-btn">
              <div>
                <label>起止时间：</label>
                <el-date-picker class="seltime" v-model="time" type="daterange" range-separator="至"
                    start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions">
                </el-date-picker>
              </div>
              <div class="search_btns">
                <el-button type="primary" icon="el-icon-download" @click="download">导出</el-button>
              </div>
            </div>
            <div class="order-tabs">
                <el-table ref="singleTable" :data="getData" highlight-current-row border
                style="width:100%;">
                    <el-table-column prop="order_no" label="订单号"></el-table-column>
                    <el-table-column prop="amount" label="充值金额"></el-table-column>
                    <el-table-column prop="fee" label="手续费"></el-table-column>
                    <el-table-column prop="arrival_amount" label="实际到账"></el-table-column>
                    <el-table-column prop="remark" label="说明"></el-table-column>
                    <el-table-column prop="vip_item_id" label="vip级别"></el-table-column>
                    <el-table-column prop="merchant_code" label="XFT商户编号"></el-table-column>
                    <el-table-column prop="create_time" label="创建时间"></el-table-column>
                    <el-table-column prop="paid_time" label="支付时间"></el-table-column>
                    <el-table-column prop="pay_status" label="支付状态">
                      <template slot-scope="scope">
                          <label v-if="scope.row.pay_status == 0" class="special">待支付</label>
                          <label v-if="scope.row.pay_status == 1" class="special2">已支付</label>
                      </template>
                    </el-table-column>
                </el-table>
                <el-pagination @current-change="handleCurrentChange" class="pagination" :page-size="10" :current-page.sync="currentRow" layout="total, prev, pager, next" :total="totalPage">
                </el-pagination>
                <el-dialog title="一键发货" :visible.sync="sentDialogVisible" width="30%">
                    <div class="des-dialog">
                        <p>此操作将一键发货，是否继续?</p>
                    </div>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="sentDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="sureAllSent()">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Header from "../common/Header";
import Aside from "../common/Aside";
import axios from "axios";
import Vue from "vue";
import commonURL from "../../assets/js/ajax";
import { upgradeToVipOrder , getUrl , exportGxOrder } from "@/api/public.js";
import moment from "moment";

export default {
  name: "orderManage",
  data() {
    return {
      loginData: {}, //登陆的数据
      id: "",
      order_no: "",
      username: "",
      time:[],
      getData: [],
      currentRow: 1,
      totalPage: 0,
      clickdata: {},
      editDialogVisible: false,
      sentDialogVisible: false,
      pay_status: 2,
      members: [
        {
          value: 2,
          label: "全部"
        },
        {
          value: 0,
          label: "待支付"
        },
        {
          value: 1,
          label: "已支付"
        }
      ],
      loading:false,
      result_list:[],
      tishiDialogVisible: false,

      pickerOptions:{
        //日期选择问题
        onPick: ({ maxDate, minDate }) => {
          this.pickerMinDate = minDate.getTime();
          if (maxDate) {
            this.pickerMinDate = "";
          }
        },
        disabledDate: time => {
          if (this.pickerMinDate !== "") {
            const day30 = (30 - 1) * 24 * 3600 * 1000;
            let maxTime = this.pickerMinDate + day30;
            if (maxTime > new Date()) {
              maxTime = new Date();
            }
            return time.getTime() > maxTime;
          }
          return time.getTime() > Date.now();
        }
      },
    };
  },
  components: {
    "Index-Header": Header,
    "Index-Aside": Aside
  },
  created() {
    this.$store.state.adminleftnavnum = "1";
  },
  mounted() {
    if(!!localStorage.getItem("loginData")){
      this.loginData = JSON.parse(localStorage.getItem("loginData"));
      var day3 = new Date();
      day3.setTime(day3.getTime()-24*60*60*1000);
      var begin = this.$moment(new Date(day3)).format("YYYY-MM-DD");
      var end = this.$moment(new Date()).format("YYYY-MM-DD");
      this.time.push(begin);
      this.time.push(end);
      this.getAllData();
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.currentRow = val;
      this.getAllData();
    },
    openDetails: function(row, event, column) {
      this.clickdata = row;
    },
    getAllData() {

      let params = {
          pay_status: this.pay_status,
          uid: this.loginData.id,
          sid: this.loginData.sid,
          order_no: this.order_no,
          username: this.username,
          page_index: this.currentRow,
          page_size: 10
      };
      upgradeToVipOrder(params).then(res => {
          if (res.data.code === 0) {
            this.getData = res.data.data.list;
            this.totalPage = Number(res.data.data.count);
            this.getData.forEach(data => {
              data.amount = Number(data.amount).toFixed(2);
              data.fee = Number(data.fee).toFixed(2);
              data.arrival_amount = Number(data.arrival_amount).toFixed(2);
              var newDate = new Date();
              newDate.setTime(data.paid_time * 1000);
              data.paid_time = this.$moment(newDate).format(
              "YYYY-MM-DD HH:mm:ss"
              );
              var newDate2 = new Date();
              newDate2.setTime(data.create_time * 1000);
              data.create_time = this.$moment(newDate2).format(
              "YYYY-MM-DD HH:mm:ss"
              );
            });
          } else {
              this.$message.error(res.data.msg);
          }
      });
    },
    //查询
    searchInfo() {
      this.getAllData();
    },
    //重置
    resetInfo() {
      this.order_no = "";
      this.username = "";
      this.currentRow = 1;
      this.getAllData();
    },
    //编辑
    editData(row) {
      this.editDialogVisible = true;
      this.clickdata = row;
    },
    //删除
    deleteData(id) {
      this.id = id;
      this.deleteDialogVisible = true;
    },

    download() {
      if (this.time=="" || this.time == null) {
        var day3 = new Date();
        day3.setTime(day3.getTime()-24*60*60*1000);
        var begin = new Date(day3);
        begin = Date.parse(begin)/1000;
        var end = new Date();
        end = Date.parse(end)/1000;
      } else {
        begin = new Date(this.time[0]);
        begin = Date.parse(begin)/1000;
        end = new Date(this.time[1]);
        end = Date.parse(end)/1000;
      }
      let params = {
          uid: this.loginData.id,
          sid: this.loginData.sid,
          start_time: begin,
          end_time: end
      };
      exportGxOrder(params).then(res => {
        const blob = new Blob([res.data])
        const fileName = 'VIP升级订单.xlsx'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      });
    },
  }
};
</script>

<style scoped>
</style>



